import {  Text } from "@chakra-ui/react";
import { useState } from "react";


export default function Email(){
  const [chnage, setChnage] = useState(false)
   const  handleCopy = () => {
     var copyText = "ganeshmore9623@gmail.com"
     navigator.clipboard.writeText(copyText);
     if(chnage=== false){
      setChnage(true)
      }
      else{
          setChnage(false)
      }
   }

    return (
        
        <Text  color={chnage ? "green.500" : ""} cursor='pointer' onClick={handleCopy}>Drop a mail @ganeshmore9623@gmail.com <span><i class="fa-solid fa-copy"></i></span></Text>
    )
}