import { HamburgerIcon } from "@chakra-ui/icons";
import { Box, Flex,  IconButton,  Menu,  MenuButton,  MenuItem,  MenuList,  SimpleGrid, Text } from "@chakra-ui/react";
import React from "react";
import { Link } from "react-scroll"
import './Main.css'

function Header3(){

   
    return (
        <Flex   w='100%' pt='2rem'  background='black'   boxShadow={'rgba(100, 100, 111, 0.2) 0px 7px 29px 0px'}  position='fixed'   color='white' h='6rem'  justifyContent='space-between' >
             <Box id="G"><Text ml='10rem' mt='-9' bgGradient='linear(to-l, #7928CA, #FF0080)' bgClip='text' fontSize='6xl' fontWeight='extrabold'>G</Text></Box>
             <Box id='head' fontWeight='600'  mr='19' display='flex' w='35%' textAlign='start' fontSize='md'   justifyContent='space-between'    >
             <Menu>
  <MenuButton
    as={IconButton}
    aria-label='Options'
    icon={<HamburgerIcon />}
    variant='outline'
    bgColor='white'
    color='black' 
    ml='15rem'
    fontSize='5xl'
  />
  <MenuList mr='-3.5rem'  fontSize='5xl' mt='1rem' fontWeight='700' w='25rem' h='35rem' color='black' bgColor='blackAlpha.500'>
    <MenuItem fontWeight='700' >
      <Link activeClass="active" to="home" spy={true} smooth={true}>Home</Link>
    </MenuItem>
    <MenuItem fontWeight='700' >
     <Link to="about" spy={true} smooth={true}>About</Link>
    </MenuItem>
    <MenuItem  fontWeight='700'>
     <Link to="skills" spy={true} smooth={true}>Skills</Link>
    </MenuItem>
    <MenuItem fontWeight='700' >
      <Link to="project1" spy={true} smooth={true}>Project</Link>
    </MenuItem>
    <MenuItem fontWeight='700' >
     <Link to="statistics" spy={true} smooth={true} >Statistics</Link>
    </MenuItem>
    <MenuItem fontWeight='700' >
     <Link to="contact" spy={true} smooth={true} >Contact</Link>
    </MenuItem>
  </MenuList>
</Menu>
             </Box>
        </Flex>
    )
}

export default Header3;